<template>
    <header>
        <h3>BME3-PDFs</h3>
        <div ref='sortBox' class='sortBox' @click='toggleShow1'>
            <h6>Sort by</h6>
            <img src='../assets/images/down-arrow.svg' alt='down arrow'>
            <div v-if='show' class='sortBox__menu'>
                <h6>Date</h6>
                <h6>Week</h6>
            </div>
        </div>
    </header>
</template>


<script>
 import { ref, onMounted } from 'vue'

export default {
    setup() {
        let show = ref(false);
        let sortBox = ref(null);
        

        function toggleShow1() {
            show.value = !show.value;
            sortBox.value.classList.toggle('remove-radius');

        }

        return {show, toggleShow1, sortBox};
    }    

}
</script>


<style scoped>
    header {
        width: 100%;
        margin-top: 38px;
        color: white;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .sortBox {
        width: 83px;
        height: 33px;
        border-radius: 20px;
        background: #503584;
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: relative;
               
    }
    .sortBox h6 {
        margin-right: -10px;
    }
    .sortBox__menu {
        position: absolute;
        left: 0;
        top: 26px;
        width: 100%;
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
        background: #503584;
        padding: 10px 0px 11px 0px;
        z-index: 10;

    }
    .sortBox__menu h6 {
        width: 83px;
        text-align: center;
    }
    .sortBox__menu h6:not(:last-child) {
        margin-bottom: 12px;
    }
    .remove-radius {
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
    }
</style>