<template>
    <div v-if='slideMenu' class='sideMenu'>
        <div class='sideMenu__close' @click='close'>
            <img src='../assets/images/close.svg' alt='close menu'>
        </div>
    </div>
</template>


<script>
export default {
    props: ['slideMenu'],

    methods: {
        close: function() {            
            this.$emit('close');
        }
    }
}
</script>

<style scoped>
    .sideMenu {
        position: absolute;
        left: 0;
        top: 0;
        width: 80%;
        height: 100%;
        background: #503584;
        display: relative;
        z-index: 100;
    }
    .sideMenu__close {
        position: absolute;
        width: max-content;
        right: 14px;
        top: 14px;
    }
</style>